const opreate={
    emitCall(callname, data,vm) {
        vm.$emit(callname,data);
      },
      ObjectCopy: objectcopy,
      //判断是否存在按钮权限
      hasLoginComponents(vm,perm){
        var userperms=vm.$store.getters.GETUSERPERMS;
        var index= userperms.indexOf(perm);
        if(index>-1){
          return true;
        }else{
          return false;
        }
      },
      //检测checkData并返回boolean
      TESTcheck(vm,title){
        var checkData = vm.checkData;
        var len = checkData.length;
        if (len > 1) {
          vm.$layer.alert(
            '<div class="layer divtext"><i class="icon icon-warning"></i>一次只能'+title+'一条数据</div>'
          );
          return false;
        } else if (len <= 0) {
          vm.$layer.alert(
            '<div class="layer divtext"><i class="icon icon-tips"></i>请选择需要'+title+'的数据</div>'
          );
           return false;
        } else {
          return true;
        }
      }
}
function objectcopy(obj) {
    var data;
    if (obj instanceof Object) {
      data = {};
    }
    if (obj instanceof Array) {
      data = [];
    }
    for (var key in obj) {
      if (typeof obj[key] == "object") {
        data[key] = objectcopy(obj[key]);
      } else {
        data[key] = obj[key];
      }
    }
    return data;
  }
export default opreate;


